.Profile{
    height: 610px;
    background-color: #fff;
    position: relative;
}

.midasi2{
    color: black;
    margin: 0;
    position: relative;
    padding-top: 40px;
    padding-left: 120px;
}

ul{
    list-style: none;
}

.keirekiul{
    line-height: 30px;
    margin-left: 80px;
}

a{
    color: black;
}

.media{
    display: flex;
    justify-content: flex-end;
    height: 50px;
    margin-right: 100px;
}

#media{
    margin: 30px;
    font-size: 40px;

}

.My{
    margin-top: 50px;
    display: flex;
}

.Face{
    width: 180px;
    height: 240px;
    margin-left: 120px;
}

